import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex)
// 
const store = new Vuex.Store({
    state: {
        address:''
    },
    //
    mutations: {
        pushin(state,h){
            state.address=h
        }
    },
    //
    getters:{
        getAddress(state){
            return state.address
        }
    }
})
export default store