import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter);

import Detail from '../page/Detail'
import Shouye from '../page/Shouye'
import Address from '../page/Adress'

const routes = [
    //
  { path: '/', redirect: { name: 'main' }},
  { path: '/shouye', redirect: { name: 'main' }},
    //
    //
    //{ path: '/detail/:id', name:'detail', component: Detail },
      //props$route
  // 
      // const User = {
      //   template: '<div>User {{ $route.params.id }}</div>'
      // }
      //
      // const User = {
      //   props: ['id'],
      //   template: '<div>User {{ id }}</div>'
      // }
  { path: '/detail/:id',
    name:'detail',
    component: Detail ,
    props:true,
   },
   { path: '/address',
    name:'address',
    component: Address 
   },
  { path: '/shouye',
   name:'shouye' , 
   component: Shouye ,
   children:[
    {
        //  /shouye/discover 
        // Discover  User  <router-view> 
        path: 'main',
        name: 'main',
        component: Shouye.components.Main
    },{
      path: 'wode',
        name: 'wode',
        component: Shouye.components.Wode
    },{
      path: 'login',
        name: 'login',
        component: Shouye.components.Login
    },{
      path: 'label',
        name: 'label',
        component: Shouye.components.Label
    }
   ] },
]

const router = new VueRouter({
  routes // ()  routes: routes
})
